/* Global Components */
.card {
  background: var(--card);
  border-radius: var(--radius);
  padding: 24px;
  box-shadow: var(--shadow);
  border: 1px solid var(--border);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--border);
}

.btn {
  padding: 8px 16px;
  border-radius: 8px;
  border: 1px solid var(--border);
  background: white;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.btn-primary {
  background: var(--primary);
  color: white;
  border-color: var(--primary);
}

.btn-primary:hover {
  background: var(--primary-hover);
}

.btn-sm {
  padding: 4px 8px;
  font-size: 13px;
}

/* Navigation */
.nav-buttons a {
  text-decoration: none;
  color: var(--text-muted);
  font-weight: 500;
  padding: 8px 12px;
  border-radius: 6px;
  transition: all 0.2s;
}

.nav-buttons a:hover {
  color: var(--primary);
  background: #f1f5f9;
}

.nav-buttons a.active {
  color: var(--primary);
  background: #eef2ff;
}

/* Tasks */
.task {
  display: flex;
  align-items: center;
  padding: 16px;
  border-radius: var(--radius);
  border: 1px solid var(--border);
  margin-bottom: 12px;
  background: white;
  transition: transform 0.1s, box-shadow 0.2s;
}

.task:hover {
  box-shadow: var(--shadow-sm);
  border-color: var(--primary);
}

.task-info {
  flex: 1;
}

.task h4 {
  margin: 0 0 4px 0;
  font-size: 16px;
}

.task-meta {
  display: flex;
  gap: 12px;
  align-items: center;
}

.priority-tag {
  font-size: 11px;
  text-transform: uppercase;
  font-weight: 700;
  padding: 2px 8px;
  border-radius: 99px;
}

.priority-high { background: #fee2e2; color: var(--danger); }
.priority-medium { background: #fef3c7; color: var(--warning); }
.priority-low { background: #dcfce7; color: var(--success); }

.status-badge {
  font-size: 12px;
  padding: 4px 10px;
  border-radius: 99px;
  background: #f1f5f9;
}

.task-completed { opacity: 0.6; }
.task-completed h4 { text-decoration: line-through; }

/* Date Boxes */
.date-section {
  margin-top: 32px;
}

.date-box {
  background: white;
  border: 1px solid var(--border);
  padding: 16px;
  border-radius: var(--radius);
  margin-bottom: 16px;
}

.date-header {
  font-weight: 600;
  color: var(--text-muted);
  margin-bottom: 12px;
  font-size: 14px;
}

/* Skill List */
.skill-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  border-bottom: 1px solid var(--border);
}

.skill-item:last-child { border-bottom: none; }

.skill-name { font-weight: 600; }

/* Utils */
.muted { color: var(--text-muted); font-size: 14px; }
.text-center { text-align: center; }
.mb-1 { margin-bottom: 4px; }
.mb-2 { margin-bottom: 8px; }
.mb-4 { margin-bottom: 16px; }

/* Forms */
input, select, textarea {
  width: 100%;
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid var(--border);
  background: #f8fafc;
  font-size: 14px;
  transition: border-color 0.2s;
}

input:focus, select:focus {
  outline: none;
  border-color: var(--primary);
  background: white;
}
